* {
    box-sizing: border-box;
}
body{
    background-color: #eff3f7;
    margin:0;
    padding:0;
    font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    height: 100%;
}
html,
body,
#root,
.container{
    height:100%;
    display:flex;
    justify-content: center;
}
.loader{
    width:100%;
    height:100%;
    display:flex;
    justify-content: center;
    align-items: center;
    font-weight: 600;
}

